.pictures {

  &__list-items {
    list-style: none;
    padding: 0;
    margin: 0;
    display: flex;

    img {
      width: 250px;
    }
  }

  &__list-item {
    margin-left: auto;
    margin-right: auto;
  }

  &__link-block {
    display: flex;
    justify-content: center;
  }

  &__link {
    font-weight: 300;
    outline: 1px solid #000000;
    padding: 5px 30px;
    &:hover {
      color: #ffffff;
      background-color: #d5d5d5;
      outline: none;
    }

    &:focus {
      opacity: 0.7;
    }
}

  @media (min-width: @tablet-width){/////////////////////////////////////////////

    &__list-items {
      img {
        width: 450px;
      }
    }

    &__link-block {
      margin-top: 30px;
    }
  }


   @media (min-width: @desktop-width){//////////////////////////////////////////
  }
}
