.page-main {

  &__wrapper {
    width: 280px;
    margin: 0 auto;
    padding: 0 20px;
    outline: 1px solid #000000;
  }



  &__block-name {
    margin-top: -400px;
  }




  @media (min-width: @tablet-width) { //////////////////////////////////////////
    margin-bottom: 50px;



  }

  @media (min-width: @desktop-width) { /////////////////////////////////////////
    border-bottom: 1px solid #d3d3d3;


    &__pictures-link {
      margin-top: 15px;
      padding: 5px 20px;
      }

    &__pictures {

      padding-bottom: 50px;
      margin-bottom: 50px;
    }
  }
}
